/* eslint-disable import/no-anonymous-default-export */
import * as actionTypes from '../actions/actionTypes';

const initialState = {
	token: localStorage.getItem('token'),
	isAuthenticated: false,
	loading: true,
	user: null,
	allClubs: [],
	allEvents: [],
	membersEvents: [],
	modal: 'none',
};

// eslint-disable-next-line import/no-anonymous-default-export
export default (state = initialState, action) => {
	switch (action.type) {
		/*------------------- Auth and User --------------------------*/
		case actionTypes.USER_LOADED:
			return {
				...state,
				isAuthenticated: true,
				loading: false,
				user: action.payload,
			};
		case actionTypes.REGISTER_SUCCESS:
		case actionTypes.LOGIN_SUCCESS:
			localStorage.setItem('token', action.payload);
			return {
				...state,
				token: action.payload,
				loading: false,
			};

		/*------------------- Clubs --------------------------*/
		case actionTypes.CLUBS_LOADING:
			return {
				...state,
				loading: true,
			};
		case actionTypes.CLUB_CREATED:
		case actionTypes.CLUB_DELETED:
		case actionTypes.APPROVE_JOIN_REQUEST:
		case actionTypes.DECLINE_JOIN_REQUEST:
		case actionTypes.REMOVE_MEMBER:
		case actionTypes.LEAVE_CLUB:
			return {
				...state,
				loading: false,
			};
		case actionTypes.FETCH_ALL_CLUBS:
			return {
				...state,
				allClubs: action.payload,
				loading: false,
			};
		// case actionTypes.DELETE_CLUB_JOIN_REQUEST:
		// 	return {
		// 		...state,
		// 		user: action.payload,
		// 	};
		// case actionTypes.CLUB_JOIN_REQUEST:
		// 	return {
		// 		...state,
		// 		user: action.payload,
		// 	};
			// return {
			// 	...state,
			// 	allClubs: action.payload,
			// 	loading: false,
			// };

		/*------------------- Events --------------------------*/
		case actionTypes.EVENTS_LOADING:
			return {
				...state,
				loading: true,
			};
		case actionTypes.FETCH_ALL_EVENTS:
			return {
				...state,
				allEvents: action.payload.eventsArr,
				membersEvents: action.payload.membersEvents,
				loading: false,
			};
		case actionTypes.JOIN_EVENT:
		case actionTypes.LEAVE_EVENT:
		case actionTypes.EVENT_CREATED:
		case actionTypes.EVENT_UPDATED:
			return {
				...state,
				loading: false,
			};

		/*------------------- Modal --------------------------*/
		case actionTypes.SHOW_MODAL:
			return {
				...state,
				modal: 'flex',
			};
		case actionTypes.HIDE_MODAL:
			return {
				...state,
				modal: 'none',
			};
		/*------------------- Errors --------------------------*/
		case actionTypes.CLUB_ERROR:
			return {
				...state,
				loading: false,
			};

		case actionTypes.EVENT_ERROR:
			return {
				...state,
				loading: false,
			};

		case actionTypes.REGISTER_FAIL:
		case actionTypes.LOGIN_FAIL:
		case actionTypes.LOGOUT:
			localStorage.clear();
			return {
				token: null,
				isAuthenticated: false,
				loading: false,
				user: {},
				allClubs: [],
				allEvents: [],
				modal: 'none',
			};

		default:
			return state;
	}
};
