import React, { useState } from 'react';
import { connect } from 'react-redux';
// import { useHistory } from 'react-router-dom';
// import ButtonsContainer from '../layout/ButtonsContainer';
import EventSmallCard from './EventSmallCard';
import Spinner from '../layout/Spinner';
import { fetchAllEvents } from '../../store/actions/eventActions';
import { joinEvent, leaveEvent } from '../../store/actions/eventActions';
import {
	joinRequest,
	cancelJoinRequest,
} from '../../store/actions/clubActions';
import { loadUser } from '../../store/actions/authActions';

const Events = (props) => {

	const [createdEvents, setCreatedEvents] = useState([]);
	const [selectedBtn, setSelectedBtn] = useState('registered');

	const eventHandler = (action, _id) => {
		switch (action) {
			case 'join':
				props.joinEvent(_id);
				break;
			case 'leave':
				props.leaveEvent(_id);
				break;
			case 'clubJoinRequest':
				props.joinRequest(_id);
				break;
			case 'cancelJoinRequest':
				props.cancelJoinRequest(_id);
				break;
			case 'edit':
				props.setEditEvent(_id);
				props.setDisplay(true);
				break;
			default:
				break;
		}
	};

	return (
		<div className='events'>
			<h3>Events</h3>
			{/* <div className='search'>
				<label htmlFor='search-inp'>
					<i className='fas fa-search'></i>
					<input
						type='text'
						name='search'
						className='search-inp'
						id='search-inp'
					/>
				</label>
			</div> */}

			<div className='cards-container'>
				{props.events.length > 0 ? (
					props.events.map((event) => {
						return (
							<EventSmallCard
								event={event}
								type={props.type}
								key={event._id}
								userPendingClubs={props.user.pendingclubrequest}
								setEvent={props.setEditEvent}
								setDisplay={props.setDisplay}
								eventHandler={eventHandler}
							/>
						);
					})
				) : (
					<p>No Events Availble</p>
				)}
				{props.regEvents.length > 0 ? (
					props.regEvents.map((event) => {
						return (
							<div>
								<EventSmallCard
									event={event}
									type={'player'}
									key={event._id}
									userPendingClubs={props.user.pendingclubrequest}
									setEvent={props.setEditEvent}
									setDisplay={props.setDisplay}
									eventHandler={eventHandler}
								/>
							</div>
						);
					})
				) : (
					<p>You didn't register to any events</p>
				)}
			</div>
		</div>
	);
};
const mapStateToProps = (state) => {
	return {
		allEvents: state.authReducer.allEvents,
		membersEvents: state.authReducer.membersEvents,
		createdClubs: state.authReducer.user.createdclubs,
		userPendingClubs: state.authReducer.user.pendingclubrequest,
		eventLoading: state.authReducer.eventLoading,
		user: state.authReducer.user,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		fetchAllEvents: () => dispatch(fetchAllEvents()),
		joinEvent: (eventId) => dispatch(joinEvent(eventId)),
		leaveEvent: (eventId) => dispatch(leaveEvent(eventId)),
		joinRequest: (club_id) => dispatch(joinRequest(club_id)),
		cancelJoinRequest: (club_id) => dispatch(cancelJoinRequest(club_id)),
		loadUser: () => dispatch(loadUser()),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Events);
