import React, { useState } from 'react';
import { useJsApiLoader } from '@react-google-maps/api';
import PlacesAutocomplete, {
	geocodeByAddress,
	getLatLng,
} from 'react-places-autocomplete';
import { GoogleMap, LoadScript } from '@react-google-maps/api';
const Address = (props) => {
	// TODO - set api key with env
	const [address, setAddress] = useState(null);
	const [coordinates, setCoordinates] = useState({ lat: '', lng: '' });
	const addressObject = {
		streetNumber: '',
		street: '',
		city: '',
		neighborhood: '',
		region: '',
		state: '',
		country: '',
		postcode: '',
		coordinates: '',
	};
	const options = {
		// types: ['(regions)'],
		// componentRestrictions: { country: 'us' },
	};

	const userPreferance = {
		region: '',
		useLoaction: true,
	};

	const addressHandler = (address) => {
		address.map((component) => {
			const componentType = component.types[0];
			switch (componentType) {
				case 'street_number':
					addressObject.streetNumber = `${component.long_name} ${addressObject.streetNumber}`;
					break;

				case 'route':
					addressObject.street += component.long_name;
					break;

				case 'postal_code':
					addressObject.postcode = `${component.long_name}${addressObject.postcode}`;
					break;

				case 'locality':
					addressObject.city = component.long_name;
					// document.querySelector('#locality').value = component.long_name;
					break;
				case 'neighborhood':
					addressObject.neighborhood = component.long_name;
					// document.querySelector('#locality').value = component.long_name;
					break;

				case 'administrative_area_level_1':
					addressObject.region = component.long_name;
					// document.querySelector('#state').value = component.short_name;
					break;

				case 'country':
					addressObject.country = component.long_name;
					break;
				default:
					return addressObject;
			}
		});
	};
	const onSelectHandler = async (selectedAddress) => {
		try {
			const result = await geocodeByAddress(selectedAddress);
			const latlng = await getLatLng(result[0]);
			setCoordinates(latlng);
			setAddress(selectedAddress);
			addressObject.coordinates = latlng;
			addressHandler(result[0].address_components);
			props.onChangeAddress(addressObject);
		} catch (error) {
			console.error('Error', error);
		}
	};

	return (
		<div>
			<LoadScript
				googleMapsApiKey='AIzaSyAyw8__eVgql-00alyPf7f-h3fdzOPzM5U'
				libraries={['places']}>
				{/* {isLoaded ? ( */}
				<PlacesAutocomplete
					value={address ? address : ''}
					onChange={setAddress}
					onSelect={onSelectHandler}>
					{({
						getInputProps,
						suggestions,
						getSuggestionItemProps,
						loading,
					}) => (
						<div className='location-search-input'>
							<input
								{...getInputProps({
									placeholder: 'Event address ...',
									className: 'search-input',
									// style: { width: '250px' },
								})}
							/>
							{/* <div>Lattidue: {coordinates.lat}</div>
							<div>Longtitude: {coordinates.lng}</div> */}
							{/* <div className='address-line'>address: {address}</div> */}
							<div>
								{loading ? <div>...Loading</div> : null}
								{suggestions.map((suggestion) => {
									const className = suggestion.active
										? 'suggestion-active'
										: 'suggestion-not-active';
									// const style = suggestion.active
									// 	? {
									// 			width: '250px',
									// 			backgroundColor: 'red',
									// 			cursor: 'pointer',
									// 			color: 'gray',
									// 	  }
									// 	: {
									// 			width: '250px',
									// 			backgroundColor: 'yellow',
									// 			cursor: 'pointer',
									// 			color: 'gray',
									// 	  };

									return (
										<div
											key={suggestion}
											{...getSuggestionItemProps(suggestion, {
												// style,
												className,
											})}>
											{suggestion.description}
										</div>
									);
								})}
							</div>
						</div>
					)}
				</PlacesAutocomplete>
				{/* ) : (
				<div></div>
			)} */}
			</LoadScript>
		</div>
	);
};

export default Address;
