import * as actionTypes from '../actions/actionTypes';

const initialState = {
	clubLoading: true,
	eventLoading: true,
	errors: [],
	members: [],
};
// TODO - decide to delete this reducer
// eslint-disable-next-line import/no-anonymous-default-export
export default (state = initialState, action) => {
	switch (action.type) {
		case actionTypes.CLUBS_LOADING:
			return {
				...state,
				clubLoading: true,
			};
		case actionTypes.CLUB_CREATED:
			return {
				...state,
				clubLoading: false,
			};

		case actionTypes.CLUB_ERROR:
			return {
				...state,
				clubLoading: false,
				errors: action.payload,
			};
			case actionTypes.EVENTS_LOADING:
			return {
				...state,
				eventLoading: true,
			};

		default:
			return state;
	}
};
