export const approveJoinRequest = (club_id, user_id) => async (dispatch) => {
	try {
		dispatch({ type: actionTypes.CLUBS_LOADING });
		await axios.put(`/api/clubs/approve-request/${club_id}/${user_id}`);

		dispatch({
			type: actionTypes.APPROVE_JOIN_REQUEST,
		});
		dispatch(fetchCreatedClub(club_id));
	} catch (error) {
		console.log(error);
		const errors = error.response.data.errors;
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};

