export const removeMember = (club_id, user_id) => async (dispatch) => {
	try {
		dispatch({ type: actionTypes.CLUBS_LOADING });
		await axios.delete(`/api/clubs/remove_member/${club_id}/${user_id}`);
		dispatch({
			type: actionTypes.REMOVE_MEMBER,
		});
		dispatch(setAlert('Member removed', 'success'));
		dispatch(loadUser());
	} catch (error) {
		console.log(error);
		const errors = error.response.data.errors;
		errors.forEach((error) => {
			console.log(error);
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};

