import * as actionTypes from './actionTypes';
import axios from 'axios';
import { setAlert } from './alretActions';
import { loadUser } from './authActions';

export const eventLoading = () => (dispatch) => {
	dispatch({
		type: actionTypes.EVENTS_LOADING,
	});
};

export const createEvent = (newEvent) => async (dispatch) => {
	dispatch(eventLoading());
	try {
		await axios.post(`/api/events/${newEvent.club}`, newEvent);
		dispatch({
			type: actionTypes.EVENT_CREATED,
		});
		dispatch(setAlert('New event created', 'success'));
	} catch (error) {
		const errors = error.response.data.errors;
		console.log(error.response);
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.EVENT_ERROR,
			payload: errors,
		});
	}
};

export const editEvent = (updateEvent) => async (dispatch) => {
	dispatch(eventLoading);
	try {
		await axios.put(
			`/api/events/update-event/${updateEvent.club._id}`,
			updateEvent
		);
		dispatch({
			type: actionTypes.EVENT_UPDATED,
		});
		dispatch(loadUser());
		dispatch(setAlert('Event updated', 'success'));
	} catch (error) {
		const errors = error.response.data.errors;
		console.log(error.response);
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.EVENT_ERROR,
			payload: errors,
		});
	}
};

export const fetchAllEvents = (user) => async (dispatch) => {
	eventLoading();
	let membersEvents = [];
	let eventsArr = [];
	// TODO correct function and logic not to include server call to fetch user
	try {
		const allEvents = await axios.get('/api/events/');
		eventsArr = allEvents.data;

		// create an Array of all events from user reg clubs
		user.regclubs.map((club) => {
			return (eventsArr = eventsArr.filter(
				(event) => event.club._id !== club._id
			));
		});
		user.regclubs.map((club) => {
			return (membersEvents = membersEvents.concat(club.events));
		});

		// create members events without reg events from reg clubs
		user.regevents.map((ev) => {
			return (membersEvents = membersEvents.filter(
				(evn) => evn._id !== ev._id
			));
		});

		dispatch({
			type: actionTypes.FETCH_ALL_EVENTS,
			payload: { membersEvents: membersEvents, eventsArr: eventsArr },
		});
	} catch (error) {
		console.log(error);
		dispatch(setAlert(TypeError, 'danger'));
		// const errors = error.response.data.errors;
		// errors.forEach((error) => {});
		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};

export const joinEvent = (eventId) => async (dispatch) => {
	dispatch(eventLoading());
	try {
		const data = await axios.put(`/api/events/join-event/${eventId}`);
		dispatch(loadUser());
		dispatch({
			type: actionTypes.JOIN_EVENT,
		});
		dispatch(setAlert('Your are registered to game', 'blue'));
	} catch (error) {
		const errors = error.response.data.errors;
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.EVENT_ERROR,
		});
	}
};

export const leaveEvent = (eventId) => async (dispatch) => {
	dispatch(eventLoading());
	try {
		const data = await axios.put(`/api/events/unjoin/${eventId}`);
		dispatch({
			type: actionTypes.LEAVE_EVENT,
		});
		dispatch(loadUser());
		dispatch(setAlert('You are no longer register', 'blue'));
	} catch (error) {
		const errors = error.response.data.errors;
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.EVENT_ERROR,
		});
	}
};
