import React from 'react';
import { connect } from 'react-redux';
import { showModal } from '../../store/actions/modalActions';

const Member = (props) => {
	const { name, email, avatar, _id } = props.member;

	const onDeclineRequest = (user) => {
		props.showModal({
			header: 'Decline Request',
			body:
				`Are you sure you want to decline ${name.toUpperCase()}` +
				'`' +
				`s Join requests?`,
			action: {
				action: 'declineJoinRequest',
				userId: user._id,
				clubId: props.clubId,
			},
			clubId: props.clubId,
			userId: user._id,
			class: 'danger',
		});
	};
	const onApproveRequest = (user) => {
		props.showModal({
			header: 'Approve Request',
			body:
				`Are you sure you want to approve ${name.toUpperCase()}` +
				'`' +
				`s Join requests?`,
			action: {
				action: 'approveJoinRequest',
				userId: user._id,
				clubId: props.clubId,
			},
			clubId: props.clubId,
			userId: user._id,
			class: 'blue',
		});
	};
	const onRemoveMemeber = (user) => {
		props.showModal({
			header: 'Un-Member',
			body: `Are you sure you want to un-member ${name.toUpperCase()}`,
			action: {
				action: 'removeMember',
				userId: user._id,
				clubId: props.clubId,
			},
			clubId: props.clubId,
			userId: user._id,
			class: 'danger',
		});
	};

	const date =
		props.type === 'pending'
			? new Date(Date.parse(props.member.reqdate)).toDateString()
			: new Date(Date.parse(props.member.joined)).toDateString();

	return (
		<div className='small-box-card profile'>
			<div className='card-title'>
				<img
					src={avatar ? avatar : '/images/default-avatar.png'}
					alt='avatar'
				/>
				<h3>{name.toUpperCase()}</h3>
			</div>
			<div className='card-body'>
				<p onClick={() => props.sendEmail(_id)}>
					<i className='fal fa-paper-plane'></i>
					{email}
				</p>
				{props.type === 'pending' ? (
					<div>
						<p
							onClick={() => onApproveRequest(props.clubId, _id)}
							className='approve'>
							<i className='fal fa-thumbs-up'></i> Approve Request
						</p>
						<p
							onClick={() => onDeclineRequest(props.member)}
							className='decline'>
							<i className='fal fa-thumbs-down'></i>
							Decline Request
						</p>
					</div>
				) : (
					<p onClick={() => onRemoveMemeber(props.member)} className='decline'>
						<i className='fal fa-thumbs-down'></i>
						Remove Member
					</p>
				)}
			</div>
		</div>
	);
};

const mapDispatchToProps = (dispatch) => {
	return {
		// approveJoinRequest: (club_id, user_id) =>
		// 	dispatch(approveJoinRequest(club_id, user_id)),
		// declineJoinRequest: (club_id, user_id) =>
		// 	dispatch(declineJoinRequest(club_id, user_id)),
		// removeMember: (club_id, user_id) =>
		// 	dispatch(declineJoinRequest(club_id, user_id)),
		showModal: (payload) => dispatch(showModal(payload)),
	};
};
export default connect(null, mapDispatchToProps)(Member);
