import React from 'react';
import { connect } from 'react-redux';
import { clubLoad } from '../../store/actions/managerActions';
import { eventLoading } from '../../store/actions/eventActions';
import { Link } from 'react-router-dom';
import ClubSmallCard from '../clubs/ClubSmallCard';
import { FiPlusCircle } from 'react-icons/fi';
import EventSmallCard from '../events/EventSmallCard';

const Admin = (props) => {
	return (
		<div className='main-container'>
			<h1 style={{ margin: '0 1rem' }}>Admin</h1>
			<div className='main-body'>
				<section className='left'>
					<div className='cards-container-header'>
						<h3>CLUBS</h3>
						<Link to={'/create-club'} onClick={() => props.clubLoad()}>
							<FiPlusCircle />{' '}
						</Link>
					</div>
					<div className='cards-container'>
						{props.createdclubs.length > 0 ? (
							props.createdclubs.map((club) => {
								return (
									<ClubSmallCard club={club} type={'manager'} key={club._id} />
								);
							})
						) : (
							<h3>No Clubs Created</h3>
						)}
					</div>
				</section>
				<section className='right'>
					<div className='cards-container-header'>
						<h3>EVENTS</h3>
						<Link to={'/create-event'} onClick={() => props.eventLoad()}>
							<FiPlusCircle /> <i className='fas fa-space-shuttle'></i>
						</Link>
					</div>
					<div className='cards-container'>
						{false ? (
							<EventSmallCard clubs={props.createdevents} type={'manager'} />
						) : (
							<h3>No Events Created</h3>
						)}
					</div>
				</section>
			</div>
		</div>
	);
};
const mapStateToProps = (state) => {
	return {
		createdclubs: state.authReducer.user.createdclubs,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		clubLoad: () => dispatch(clubLoad()),
		eventLoad: () => dispatch(eventLoading()),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Admin);
