import React from 'react';
import { Link } from 'react-router-dom';

const ClubSmallCard = (props) => {
	// TODO- fix delete route - remove from users and manager
	// eslint-disable-next-line no-lone-blocks
	const { name, _id, description, events, members, pendingRequest, clubLogo } =
		props.club;
	let btn;
	switch (props.type) {
		case 'members':
			btn = (
				<ul>
					<li>
						<Link
							to={{
								pathname: '/events',
								eventHandler: props.eventHandler,
								events: events,
							}}>
							<div>
								<i className='fal fa-diamond'></i>
								<p>Events</p>
							</div>
						</Link>
					</li>
					<li>
						<div onClick={() => props.eventHandler('leave', _id)}>
							<i className='fal fa-sign-out'></i>
							<p>Leave</p>
						</div>
					</li>
				</ul>
			);
			break;
		case 'pending':
			btn = (
				<ul>
					<li>
						<div
							onClick={() => {
								props.eventHandler('cancelJoinRequest', _id);
							}}>
							<i className='fal fa-times' id='Requests'></i>
							<p>cancel Request</p>
						</div>
					</li>
				</ul>
			);
			break;
		case 'non-member':
			btn = (
				<ul>
					<li>
						<div onClick={() => props.eventHandler('clubJoinRequest', _id)}>
							<i className='fal fa-paper-plane'></i>
							<p>Join Request</p>
						</div>
					</li>
				</ul>
			);
			break;
		case 'manager':
			btn = (
				<ul>
					<li>
						<div onClick={() => props.eventHandler('edit', _id)}>
							<i className='fal fa-edit'></i>
							<p>Edit</p>
						</div>
					</li>
				</ul>
			);
			break;
		default:
			break;
	}

	return (
		<div className='profile-card'>
			<div
				className='profile-header background-div'
				style={{ backgroundImage: `url(${clubLogo})` }}>
				<h3>{name}</h3>
			</div>
			<div className='btn-icons'>{btn}</div>
			<div className='profile-body'>
				{events ? (
					<p>Current Events: {events.length}</p>
				) : (
					<p>No events published</p>
				)}
				{members ? (
					<p>Members: {members.length}</p>
				) : (
					<p>No Members Register</p>
				)}
				{props.type === 'manager' ? (
					pendingRequest ? (
						<p>Pending Requests: {pendingRequest.length}</p>
					) : (
						<p>No Pending Requests</p>
					)
				) : null}
			</div>
			{/* <div className='profile-footer'>
				<button className='btn-light-outline'>More</button>
			</div> */}
		</div>
	);
};

export default ClubSmallCard;
