import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { setAlert } from '../../../store/actions/alretActions';
import { register } from '../../../store/actions/authActions';

const Register = (props) => {
	const [formData, setFormData] = useState({
		name: '',
		email: '',
		password: '',
		password2: '',
	});

	const { name, email, password, password2 } = formData;

	const onChangeHandler = (e) =>
		setFormData({
			...formData,
			[e.target.name]: e.target.value,
		});
	const onSubmitHandler = () => {
		if (password !== password2) {
			props.onAlert('Password do not match', 'danger');
		} else {
			const newUser = {
				name,
				email,
				password,
			};
			props.onRegister(newUser);
		}
	};
	// Redirect if Logedin
	if (props.isAuthenticated) {
		return <Redirect to='/dashbord' />;
	}
	return (
		<div className='register'>
			<div>
				<h1 className='large text-primary'>PokerPlace</h1>
				<h1 className='large text-primary'>Sign Up</h1>
			</div>
			<form>
				<p>Create Your Account</p>
				<div>
					<input
						type='text'
						placeholder='Name'
						name='name'
						required
						value={name}
						onChange={(e) => onChangeHandler(e)}
					/>
				</div>
				<div>
					<input
						type='email'
						placeholder='Email Address'
						name='email'
						required
						value={email}
						onChange={(e) => onChangeHandler(e)}
					/>
				</div>

				<div>
					<input
						type='password'
						placeholder='Password'
						name='password'
						minLength='6'
						required
						value={password}
						onChange={(e) => onChangeHandler(e)}
					/>
				</div>
				<div>
					<input
						type='password'
						placeholder='Confirm Password'
						name='password2'
						minLength='6'
						required
						value={password2}
						onChange={(e) => onChangeHandler(e)}
					/>
				</div>
			</form>
			<div className='submit-div'>
				<button
					className='btn-primary-outline'
					onClick={() => onSubmitHandler()}>
					Register
				</button>
				<p className='m-1'>
					Already have an account? <Link to='/login'>Sign In</Link>
				</p>
			</div>
		</div>
	);
};

const mapDispatchToProps = (dispatch) => {
	return {
		onAlert: (msg, type) => dispatch(setAlert(msg, type)),
		onRegister: (newUser) => dispatch(register(newUser)),
	};
};
const mapStateToProps = (state) => ({
	isAuthenticated: state.authReducer.isAuthenticated,
});
export default connect(mapStateToProps, mapDispatchToProps)(Register);
