import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import { getAllClubs } from '../../store/actions/clubActions';
import { fetchAllEvents } from '../../store/actions/eventActions';
import { loadUser } from '../../store/actions/authActions';
import { logout } from '../../store/actions/authActions';

const NavBar = (props) => {
	const [toggleMenu, setToggleMenu] = useState(false);
	const history = useHistory();

	const guestLinks = (
		<ul>
			<li>
				<Link
					to='/about'
					onClick={() => {
						setToggleMenu(!toggleMenu);
					}}>
					About
				</Link>
			</li>
			<li>
				<Link
					to='/register'
					onClick={() => {
						setToggleMenu(!toggleMenu);
					}}>
					Register
				</Link>
			</li>
			<li>
				<Link
					to='/login'
					onClick={() => {
						setToggleMenu(!toggleMenu);
					}}>
					Login
				</Link>
			</li>
		</ul>
	);

	const logedInLinks = (
		<ul className='menu-links'>
			<li>
				<Link
					to='/clubs'
					onClick={() => {
						props.getAllClubs(props.user);
						setToggleMenu(!toggleMenu);
					}}>
					<span>
						<i className='fal fa-club'></i>
					</span>
					Clubs
				</Link>
			</li>
			<li>
				<Link
					to='/'
					onClick={() => {
						setToggleMenu(!toggleMenu);
						// props.fetchAllEvents();
					}}>
					<span>
						<i className='fal fa-diamond'></i>
					</span>
					Events
				</Link>
			</li>
			<li>
				<Link
					to='/admin'
					onClick={() => {
						setToggleMenu(!toggleMenu);
						props.getAllClubs(props.user);
					}}>
					<span>
						<i className='fal fa-spade'></i>
					</span>
					Manage
				</Link>
			</li>
			<li>
				<Link
					to='/about'
					onClick={() => {
						setToggleMenu(!toggleMenu);
					}}>
					<span>
						<i className='fal fa-heart'></i>
					</span>
					About
				</Link>
			</li>
			<li>
				<Link
					to='/'
					onClick={() => {
						props.loadUser();
						setToggleMenu(!toggleMenu);
					}}>
					<span>
						<i className='fal fa-home-lg-alt'></i>
					</span>
					Home
				</Link>
			</li>
			<li>
				<Link
					to='/'
					onClick={() => {
						setToggleMenu(!toggleMenu);
						props.onLogout();
					}}>
					<span className='hide-sm'>
						<i className='fal fa-sign-out'></i>
					</span>
					Logout
				</Link>
			</li>
		</ul>
	);

	return (
		<div className='navbar bg-dark'>
			<div className='navbar-head'>
				<div className='navbar-title'>
					<div
						className='go-back'
						onClick={() => history.goBack()}
						style={
							props.isAuthenticated ? { display: 'flex' } : { display: 'none' }
						}>
						<i className='fal fa-long-arrow-left'></i>
						<p>Back</p>
					</div>
					<Link to='#!'>
						{props.isAuthenticated ? (
							<div className='card-rows'>
								<img src={props.user.avatar} alt='avatar' />
								<h4>Hi {props.user.name.toUpperCase()}</h4>
							</div>
						) : (
							<div className='card-rows'>
								<img src='/images/default-avatar.png' alt='avatar' />
								<h4>
									<span> Hello Guest </span>
								</h4>
							</div>
						)}
					</Link>
				</div>
				<div
					className='hamburger'
					onClick={() => {
						setToggleMenu(!toggleMenu);
					}}>
					<span>
						<i className='fas fa-bars'></i>
					</span>
				</div>
			</div>
			<div
				className={toggleMenu ? 'menu-items m-fadeIn' : 'menu-items m-fadeOut'}>
				{props.isAuthenticated ? logedInLinks : guestLinks}
			</div>
		</div>
	);
};

const mapStateToProps = (state) => {
	return {
		isAuthenticated: state.authReducer.isAuthenticated,
		isLoading: state.authReducer.loading,
		user: state.authReducer.user,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		onLogout: () => dispatch(logout()),
		getAllClubs: (user) => dispatch(getAllClubs(user)),
		fetchAllEvents: () => dispatch(fetchAllEvents()),
		loadUser: () => {
			dispatch(loadUser());
		},
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(NavBar);
