import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

// Componnets
import Landing from './components/layout/Landing';
import NavBar from './components/layout/NavBar';
import FooterBar from './components/layout/FooterBar';
import Login from './components/layout/auth/Login';
import Alert from './components/layout/Alert.js';
import Registration from './components/layout/auth/Registeration';
import DashBoard from './components/layout/DashBoard';
import Clubs from './components/clubs/Clubs.jsx';
import CreateClub from './components/clubs/CreateClub';
import EditClub from './components/clubs/EditClub';
import CreateEvent from './components/events/createEvent/CreateEvent';
import EditEvent from './components/events/EditEvent';
import Events from './components/events/Events.jsx';
import Event from './components/events/Event';
import Admin from './components/admin/Admin';
import Members from './components/members/Members';
import Modal from './components/layout/Modal';
import './App.css';
import 'react-datetime/css/react-datetime.css';
// Redux imports
import { Provider } from 'react-redux';
import { loadUser } from './store/actions/authActions';
import store from './store/store';

const App = () => {
	if (localStorage.token) {
		store.dispatch(loadUser());
	}

	return (
		<Provider store={store}>
			<Router>
				<NavBar />
				<Alert />
				<Modal />
				<Route exact path='/' component={Landing} />
				<Switch>
					<Route
						path='/dashboard'
						component={() =>
							store.getState().authReducer.isAuthenticated ? (
								<DashBoard
									isAuth={store.getState().authReducer.isAuthenticated}
								/>
							) : (
								<Landing />
							)
						}
					/>
					<Route exact path='/login' component={Login} />
					<Route exact path='/register' component={Registration} />
					<Route exact path='/admin' component={Admin} />
					<Route exact path='/create-club' component={CreateClub} />
					<Route exact path='/edit-club/:id' component={EditClub} />
					<Route exact path='/clubs' component={Clubs} />
					<Route exact path='/events' component={Events} />
					<Route exact path='/event' component={Event} />
					<Route exact path='/create-event/:club_id' component={CreateEvent} />
					<Route exact path='/edit-event' component={EditEvent} />
					<Route exact path='/members/:club_name' component={Members} />
				</Switch>
				<FooterBar />
			</Router>
		</Provider>
	);
};

export default App;
