import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Redirect, useHistory } from 'react-router-dom';
import ButtonsContainer from './ButtonsContainer';
// import ClubSmallCard from '../clubs/ClubSmallCard';
import EditEvent from '../events/EditEvent';
import Events from '../events/Events';
import { loadUser } from '../../store/actions/authActions';
import {
	getAllClubs,
	leaveClub,
	joinRequest,
	cancelJoinRequest,
} from '../../store/actions/clubActions';
import Clubs from '../clubs/Clubs';

const DashBoard = (props) => {
	const { regclubs, pendingclubrequest, createdclubs } = props.user;
	const [clubs, setClubs] = useState(regclubs ? regclubs : props.allClubs);
	const [type, setType] = useState(regclubs ? 'members' : 'non-member');
	const [isPending, setIsPending] = useState(false);
	const [events, setEvents] = useState([]);
	const [regEvents, setRegEvents] = useState([]);
	const [selectedBtn, setSelectedBtn] = useState('registered');
	const [display, setDisplay] = useState(false);
	const [editEvent, setEditEvent] = useState({});
	const history = useHistory();

	const aggregateEvents = (clubsArr) => {
		let aggrEvents = [];
		clubsArr.map((club) => {
			return (aggrEvents = [...aggrEvents, ...club.events]);
		});
		return aggrEvents;
	};

	const membersEvents = (regClubs) => {
		let allEvents = aggregateEvents(regClubs);
		props.user.regevents.map((evnt) => {
			return (allEvents = allEvents.filter((ev) => ev._id !== evnt._id));
		});
		return allEvents;
	};

	useEffect(() => {
		switch (type) {
			case 'members':
				setClubs(regclubs);
				setIsPending(false);
				setEvents(membersEvents(regclubs));
				setRegEvents(props.user.regevents);
				break;
			case 'pending':
				setClubs(pendingclubrequest);
				setIsPending(true);
				setEvents(aggregateEvents(pendingclubrequest));
				break;
			case 'non-member':
				setClubs(props.allClubs);
				setIsPending(false);
				setEvents(aggregateEvents(props.allClubs));
				break;
			case 'manager':
				setClubs(createdclubs);
				setIsPending(false);
				setEvents(aggregateEvents(createdclubs));
				break;
			default:
				return;
		}
	}, [type, props.user, props.allClubs]);

	useEffect(() => {
		setEvents(membersEvents(regclubs));
		setRegEvents(props.user.regevents);
	}, []);

	const changeHandler = (e) => {
		switch (e.target.id) {
			case 'Membership':
				setType('members');
				break;
			case 'Requests':
				setType('pending');
				break;
			case 'Other':
				setType('non-member');
				break;
			case 'Created':
				setType('manager');
				break;
			default:
				return;
		}
	};

	const eventHandler = async (action, clubId) => {
		switch (action) {
			case 'leave':
				props.leaveClub(clubId);
				break;
			case 'cancelJoinRequest':
				props.cancelJoinRequest(clubId);
				break;
			case 'clubJoinRequest':
				props.joinRequest(clubId);
				break;
			case 'edit':
				history.push(`/edit-club/${clubId}`);
				break;
			default:
				break;
		}
	};

	if (!props.isAuthenticated) {
		return <Redirect to='/' />;
	}

	return (
		<div className='dashboard'>
			<div className='clubs'>
				<h3>Clubs</h3>
				<div className='circle-container'>
					<div className='small-circle'>{regclubs ? regclubs.length : 0}</div>
					<div className='small-circle'>
						{pendingclubrequest ? pendingclubrequest.length : 0}
					</div>
					<div className='small-circle'>
						{props.allClubs ? props.allClubs.length : 0}
					</div>
					<div className='small-circle'>
						{props.user.createdclubs ? props.user.createdclubs.length : 0}
					</div>
				</div>
				<ButtonsContainer
					title1='Membership'
					title2='Requests'
					title3='Other'
					title4='Created'
					func={changeHandler}></ButtonsContainer>
				<div className='cards-container'>
					<Clubs
						user={props.user}
						type={type}
						clubs={clubs}
						eventHandler={eventHandler}
					/>
				</div>
			</div>
			<hr />
			<Events
				type={type}
				isPending={isPending}
				events={events}
				regEvents={regEvents}
				setEditEvent={setEditEvent}
				setDisplay={setDisplay}
			/>
			{display ? (
				<EditEvent
					event={editEvent}
					setDisplay={setDisplay}
					selectedBtn={setSelectedBtn}
				/>
			) : null}
		</div>
	);
};

const mapStateToProps = (state) => {
	return {
		user: state.authReducer.user,
		isAuthenticated: state.authReducer.isAuthenticated,
		token: state.authReducer.token,
		loading: state.authReducer.loading,
		allClubs: state.authReducer.allClubs,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		loadUser: () => dispatch(loadUser()),
		joinRequest: (club_id) => dispatch(joinRequest(club_id)),
		cancelJoinRequest: (club_id) => dispatch(cancelJoinRequest(club_id)),
		leaveClub: (club_id) => dispatch(leaveClub(club_id)),
		getAllClubs: (user) => dispatch(getAllClubs(user)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(DashBoard);
