import React from 'react';

const ButtonsContainer = (props) => {
	return (
		<div className='buttons-container'>
			<form>
				<input
					defaultChecked
					type='radio'
					id={props.title1}
					name='selected'
					onChange={(e) => props.func(e)}></input>

				<label
					style={props.title1 ? { display: 'block' } : { display: 'none' }}
					className='btn-light-outline'
					htmlFor={props.title1}>
					{props.title1}
				</label>
				<input
					onChange={(e) => props.func(e)}
					type='radio'
					className='btn-light-outline'
					id={props.title2}
					name='selected'></input>
				<label
					style={props.title2 ? { display: 'block' } : { display: 'none' }}
					className='btn-light-outline'
					htmlFor={props.title2}>
					{props.title2}
				</label>
				<input
					onChange={(e) => props.func(e)}
					type='radio'
					className='btn-light-outline'
					id={props.title3}
					name='selected'></input>
				<label
					style={props.title3 ? { display: 'block' } : { display: 'none' }}
					className='btn-light-outline'
					htmlFor={props.title3}>
					{props.title3}
				</label>
				<input
					onChange={(e) => props.func(e)}
					type='radio'
					className='btn-light-outline'
					id={props.title4}
					name='selected'></input>
				<label
					style={props.title4 ? { display: 'block' } : { display: 'none' }}
					className='btn-light-outline'
					htmlFor={props.title4}>
					{props.title4}
				</label>
			</form>
		</div>
	);
};

export default ButtonsContainer;
