import React from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { showModal } from '../../store/actions/modalActions';

const MemberCard = (props) => {
	const history = useHistory();
	const { name, email, avatar, _id, regevents } = props.member;

	const buttons = () => {
		switch (props.type) {
			case 'member':
				return (
					<ul>
						<li>
							<div
								className='btn-delete'
								onClick={() => {
									props.removeMember(props.member);
									history.push(`edit-club/${props.clubId}`);
								}}>
								<i className='fal fa-times'></i>
								<p>Remove member</p>
							</div>
						</li>
					</ul>
				);
			case 'pending':
				return (
					<ul>
						<li>
							<div
								onClick={() => {
									props.approveRequest(props.member);
									history.push(`edit-club/${props.clubId}`);
								}}>
								<i className='fal fa-thumbs-up'></i>
								<p>Approve Request</p>
							</div>
						</li>
						<li>
							<div
								onClick={() => {
									props.declineRequest(props.member);
									history.push(`/edit-club/${props.clubId}`);
								}}>
								<i className='fal fa-thumbs-down'></i>
								<p>Decline Request</p>
							</div>
						</li>
					</ul>
				);
			default:
				return;
		}
	};

	return (
		<div className='profile-card members'>
			<div className='profile-header'>
				{/* <div className='background-div'></div> */}
				<img
					className='avatar'
					src={avatar ? avatar : 'default-avatar.png'}
					alt='avatar'
				/>
			</div>
			<div className='profile-body'>
				<h3>{name.toUpperCase()}</h3>
				{props.type === 'member' ? (
					<p>
						<strong>{regevents.length}</strong> Events attandent
					</p>
				) : null}
				<p onClick={() => props.sendEmail(_id)}>
					<i className='fal fa-paper-plane'></i> {email}
				</p>
			</div>

			<div className='profile-footer'>
				<div className='btn-icons'>{buttons()}</div>
			</div>
		</div>
	);
};

const mapDispatchToProps = (dispatch) => {
	return {
		showModal: (payload) => dispatch(showModal(payload)),
	};
};
export default connect(null, mapDispatchToProps)(MemberCard);
