import React, { useState } from 'react';
import { connect } from 'react-redux';

const EventType = (props) => {
	const [formData, setFormData] = useState(
		props.event
			? props.event
			: {
					smallBlind: '',
					bigBlind: '',
					minBuyin: '',
					maxBuyin: '',
					buyIn: '',
					startingChips: '',
					ante: '',
					timePerLevel: '',
					registerUntil: '',
					numberOfRe_entry: '',
			  }
	);

	const onChangeHandler = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const onsubmit = (data) => {
		props.eventTypeData(data);
		props.setShowComp(false);
	};
	const event = (type) => {
		switch (type.toLowerCase()) {
			case 'tournament':
				return (
					<form>
						<p>Stracture</p>
						<div className='card-rows stracture'>
							<input
								type='number'
								placeholder='buyin'
								name='buyIn'
								value={formData.buyIn}
								onChange={(e) => onChangeHandler(e)}
							/>
							<p>/</p>
							<input
								type='number'
								placeholder='starting-chips'
								name='startingChips'
								value={formData.startingChips}
								onChange={(e) => onChangeHandler(e)}
							/>
							<p>/</p>
							<input
								type='text'
								placeholder='time-per-level'
								name='timePerLevel'
								value={formData.timePerLevel}
								onChange={(e) => onChangeHandler(e)}
							/>
						</div>

						<p>Starting-Blinds</p>
						<div className='card-rows'>
							<input
								type='number'
								placeholder='small-blind'
								name='smallBlind'
								value={formData.smallBlind}
								onChange={(e) => onChangeHandler(e)}
							/>
							<p>/</p>
							<input
								type='number'
								placeholder='big-blind'
								name='bigBlind'
								value={formData.bigBlind}
								onChange={(e) => onChangeHandler(e)}
							/>
							<p>/</p>
							<input
								type='number'
								placeholder='ante'
								name='ante'
								value={formData.ante}
								onChange={(e) => onChangeHandler(e)}
							/>
						</div>

						<p>Info</p>
						<div className='card-rows'>
							<input
								type='text'
								placeholder='late-reg'
								name='registerUntil'
								value={formData.registerUntil}
								onChange={(e) => onChangeHandler(e)}
							/>
							<input
								type='text'
								placeholder='re-entry'
								name='numberOfRe_entry'
								value={formData.numberOfRe_entry}
								onChange={(e) => onChangeHandler(e)}
							/>
						</div>
					</form>
				);
			case 'cash game':
				return (
					<form>
						<p>Blinds</p>
						<div className='card-rows'>
							<div>
								<input
									type='number'
									placeholder='small-blind'
									name='smallBlind'
									value={formData.smallBlind}
									onChange={(e) => onChangeHandler(e)}
								/>
							</div>
							<p>/</p>
							<div>
								<input
									type='number'
									placeholder='big-blind'
									name='bigBlind'
									value={formData.bigBlind}
									onChange={(e) => onChangeHandler(e)}
								/>
							</div>
						</div>
						<p>Buyins</p>
						<div className='card-rows'>
							<div>
								<input
									type='number'
									placeholder='max-buyin'
									name='maxBuyin'
									value={formData.maxBuyin}
									onChange={(e) => onChangeHandler(e)}
								/>
							</div>
							<p>/</p>
							<div>
								<input
									type='number'
									placeholder='min-buyin'
									name='minBuyin'
									value={formData.minBuyin}
									onChange={(e) => onChangeHandler(e)}
								/>
							</div>
						</div>
					</form>
				);

			default:
				return;
		}
	};

	return (
		<div className='edit-modal-main event-type'>
			<div className='btn-delete' onClick={() => props.setShowComp(false)}>
				<i className='fal fa-times'></i>
			</div>
			{event(props.type)}
			<div className='edit-modal-footer'>
				<button
					className='btn-light-outline'
					onClick={() => onsubmit(formData)}>
					Submit
				</button>
			</div>
		</div>
	);
};
const mapStateToProps = (state) => {
	return {
		showComponent: state.modalReducer.showEvent,
	};
};

export default connect(mapStateToProps)(EventType);
