import React from 'react';
import { useHistory } from 'react-router';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { joinEvent, leaveEvent } from '../../store/actions/eventActions';
import { joinRequest } from '../../store/actions/clubActions';
import { loadUser } from '../../store/actions/authActions';

const Event = (props) => {
	const { type, event } = props.history.location;
	const {
		club,
		_id,
		eventLogo,
		eventName,
		eventDate,
		location,
		description,
		gameType,
		playersPerTable,
		numberOfTables,
		currency,
		cashGame,
		tournament,
	} = event;
	let date = new Date(eventDate);
	const formateddate =
		date.getDate() + '-' + (date.getMonth() + 1) + '-' + date.getFullYear();
	const formatedtime = date.getHours() + ':' + date.getMinutes();
	const history = useHistory();

	const eventHandler = (type) => {
		switch (type) {
			case 'join':
				props.joinEvent(_id);
				history.push('/');
				break;
			case 'leave':
				props.leaveEvent(_id);
				history.push('/dashboard');
				break;
			case 'clubJoinRequest':
				props.joinRequest(props.club._id);
				history.push('/');
				break;
			default:
				break;
		}
	};

	const currencyIcon = () => {
		switch (currency) {
			case 'ILS':
				return <i className='fal fa-shekel-sign'></i>;
			case 'USD':
				return <i className='fal fa-dolar-sign'></i>;
			case 'ERU':
				return <i className='fal fa-euro-sign'></i>;
			default:
				return;
		}
	};

	const buttons = () => {
		switch (type) {
			case 'member':
				return (
					<ul>
						<li>
							<span onClick={() => eventHandler('join')}>
								<i className='fal fa-chair-office'></i>
							</span>
						</li>
					</ul>
				);
			case 'non-member':
				return (
					<ul>
						<li>
							<span onClick={() => eventHandler('clubJoinRequest')}>
								<i className='fal fa-paper-plane'></i>
							</span>
						</li>
					</ul>
				);
			case 'manager':
				return (
					<ul>
						<li>
							<span onClick={() => eventHandler('edit')}>
								<i className='fal fa-edit'></i>
							</span>
						</li>
					</ul>
				);
			case 'player':
				return (
					<ul>
						<li>
							<Link
								to={{
									pathname: '/event',
									event: props.event,
									eventHandler: eventHandler,
								}}>
								<i className='fal fa-info'></i>
							</Link>
							More
						</li>
						<li>
							<span onClick={() => eventHandler('leave')}>
								<i className='fal fa-sign-out'></i>
							</span>
							Leave
						</li>
					</ul>
				);
			default:
				return;
		}
	};
	return (
		<div className='event'>
			<div className='profile-card event-card'>
				<div className='profile-header'>
					<div>
						<div
							className='background-div'
							style={{
								backgroundImage: `url(${
									eventLogo ? eventLogo : '/images/rip.jpg'
								})`,
							}}>
							<img src={club.clubLogo} alt='logo' />
						</div>
					</div>
				</div>
				<div className='profile-body'>
					<h1>{eventName}</h1>
					{cashGame ? (
						<div>
							<p>{gameType} Cash Game</p>
							<p>
								Blinds: {cashGame.smallBlind}/{cashGame.bigBlind}{' '}
								{currencyIcon()}
							</p>
						</div>
					) : (
						<div>
							<p>{gameType} Tournament</p>
							<p>
								Buyin: {tournament.buyIn} {currencyIcon()}
							</p>
						</div>
					)}
					<div>
						<p>
							Start: {formateddate} <i className='far fa-clock'></i>{' '}
						</p>
						<p>
							Availble Seats <strong>3/27</strong>
						</p>
						<p>
							Description <strong>{description}</strong>
						</p>
						<p>
							Players Per Table <strong>{playersPerTable}</strong>
						</p>
						<p>
							Number of Tables <strong>{numberOfTables}</strong>
						</p>
						<div className='players'>
							<p>Reg Players</p>
							<div className='players-img'>
								<img src={club.clubLogo} alt='player' />
								<img src={club.clubLogo} alt='player' />
								<img src={club.clubLogo} alt='player' />
							</div>
						</div>
					</div>
				</div>
				<div className='btn-icons'>{buttons()}</div>
			</div>
		</div>
	);
};

const mapDispatchToProps = (dispatch) => {
	return {
		joinEvent: (eventId) => dispatch(joinEvent(eventId)),
		leaveEvent: (eventId) => dispatch(leaveEvent(eventId)),
		joinRequest: (club_id) => dispatch(joinRequest(club_id)),
		loadUser: () => dispatch(loadUser()),
	};
};

export default connect(null, mapDispatchToProps)(Event);
