import React, { useEffect } from 'react';
import Member from './Member';
import Spinner from '../layout/Spinner';
import { connect } from 'react-redux';

const Members = (props) => {
	return (
		<div className='main-container'>
			<h2 className='large text-primary'>{props.club.name}</h2>
			<div className='main-body'>
				<div className='big-container'>
					<h5 className='hide-sm'>Pending Requests</h5>
					<div className='cards-container'>
						{props.club.pendingRequest.length > 0 ? (
							props.club.pendingRequest.map((user) => {
								return (
									<Member
										key={user._id}
										member={user}
										type='pending'
										clubId={props.club._id}
									/>
								);
							})
						) : (
							<p>NO PENDING REQUESTS</p>
						)}
					</div>
				</div>
				<div className='big-container'>
					<h5 className='hide-sm'>Club Members</h5>
					<div className='cards-container'>
						{props.club.members !== undefined ? (
							props.club.members.length > 0 ? (
								props.club.members.map((user) => {
									return (
										<Member
											key={user._id}
											member={user}
											type='members'
											clubId={props.club._id}
										/>
									);
								})
							) : (
								<p>NO MEMBERS</p>
							)
						) : (
							<Spinner />
						)}
					</div>
				</div>
			</div>
		</div>
	);
};
const mapStateToProps = (state) => {
	return {
		club: state.authReducer.createdClub,
		clubLoading: state.authReducer.clubLoading,
	};
};

export default connect(mapStateToProps)(Members);
