import * as actionTypes from './actionTypes';
import axios from 'axios';
import { setAlert } from './alretActions';
import setAuthToken from '../../utils/setAuthToken';
import { fetchAllEvents } from './eventActions';
import { getAllClubs } from './clubActions';

// auth and load user
export const loadUser = () => async (dispatch) => {
	if (localStorage.token) {
		setAuthToken(localStorage.token);
	}
	try {
		const res = await axios.get(
			'https://club-royal.herokuapp.com/api/users/loaduser'
		);
		dispatch({
			type: actionTypes.USER_LOADED,
			payload: res.data,
		});
		dispatch(getAllClubs(res.data));
		dispatch(fetchAllEvents(res.data));
	} catch (error) {
		console.log(error);
		dispatch(setAlert(error.response.data.msg, 'danger'));
	}
};
// Regiter
export const register = (newUser) => async (dispatch) => {
	try {
		const res = await axios.post('/api/users/register', newUser);
		dispatch({
			type: actionTypes.REGISTER_SUCCESS,
			payload: res.data,
		});
		dispatch(loadUser());
	} catch (error) {
		dispatch(setAlert(error.response.data.msg, 'danger'));
		dispatch({
			type: actionTypes.REGISTER_FAIL,
		});
	}
};

// Login
export const login = (loggedUser) => async (dispatch) => {
	try {
		const res = await axios.post('/api/users/login', loggedUser);
		dispatch({
			type: actionTypes.LOGIN_SUCCESS,
			payload: res.data.token,
		});
		dispatch(loadUser());
	} catch (error) {
		dispatch(setAlert(error.response.data.msg, 'danger'));
		dispatch({
			type: actionTypes.LOGIN_FAIL,
		});
	}
};

//  Logout and clear
export const logout = () => (dispatch) => {
	dispatch({
		type: actionTypes.LOGOUT,
	});
};
export const reLoadUser = (user) => (dispatch) => {
	dispatch({
		type: actionTypes.USER_LOADED,
		payload: user,
	});
};
