import React, { useState } from 'react';
import { connect } from 'react-redux';
import { openEventType } from '../../store/actions/modalActions';
import { editEvent } from '../../store/actions/eventActions';
import { useJsApiLoader } from '@react-google-maps/api';
import EventType from './createEvent/EventType';
import Address from '../address/Address';
import MapComponent from '../address/MapComponent';
import { fromAddress } from 'react-geocode';
import Dater from '../../utils/Dater';
import { useHistory } from 'react-router-dom';

const EditEvent = (props) => {
	const history = useHistory();
	const [showTypeComp, setShowTypeComp] = useState(false);
	const [formData, setFormData] = useState(
		props.event
			? props.event
			: {
					eventName: '',
					eventDate: '',
					location: {
						street: '',
						streetNumber: '',
						city: '',
						neighborhood: '',
						country: '',
						state: '',
						coordinates: '',
					},
					description: '',
					gameType: '',
					eventType: '',
					playersPerTable: 9,
					numberOfTables: 1,
					currency: 'ILS',
					remarks: '',
					smallBlind: '',
					bigBlind: '',
					minBuyin: '',
					maxBuyin: '',
					buyIn: '',
					startingChips: '',
					ante: '',
					timePerLevel: '',
					registerUntil: '',
					numberOfRe_entry: '',
			  }
	);

	const onChangeHandler = (e) => {
		if (e.target === undefined) {
			setFormData({
				...formData,
				eventDate: e.toString(),
			});
			return;
		}
		setFormData({
			...formData,
			[e.target.name]: e.target.value,
		});
	};

	const onChangeAddress = (e) => {
		setFormData({ ...formData, location: e });
	};

	const setFields = (data) => {
		if (formData.tournament) {
			setFormData({ ...formData, tournament: { ...data } });
		} else {
			setFormData({ ...formData, cashGame: { ...data } });
		}
	};

	const { isLoaded } = useJsApiLoader({
		id: 'google-map-script',
		googleMapsApiKey: process.env.REACT_APP_GOOGLE_API_KEY,
		libraries: ['places'],
	});

	const {
		eventName,
		eventDate,
		location: {
			street,
			streetNumber,
			city,
			neighborhood,
			country,
			state,
			coordinates,
		},
		description,
		gameType,
		playersPerTable,
		numberOfTables,
		currency,
	} = formData;

	const submit = () => {
		props.editEvent(formData);
		history.push('/');
	};

	return (
		<div className='edit-modal-main'>
			{showTypeComp ? (
				<EventType
					type={formData.tournament ? 'tournament' : 'cash game'}
					eventTypeData={(data) => setFields(data)}
					setShowComp={setShowTypeComp}
					event={formData.tournament ? formData.tournament : formData.cashGame}
				/>
			) : null}
			{/* Form Start */}
			<form>
				<div className='edit-modal-head'>
					<div className='card-rows'>
						<input
							type='text'
							placeholder='Event Name'
							name='eventName'
							required
							defaultValue={eventName}
							onChange={(e) => onChangeHandler(e)}
						/>
					</div>
					{/* <i className='fal fa-pen'></i> */}
				</div>
				<div className='edit-modal-body'>
					<div className='card-rows'>
						<p>Start At: </p>
						<Dater
							onChangeHandler={onChangeHandler}
							date={eventDate ? eventDate : null}
						/>
					</div>
					<div className='card-rows'>
						<p>Event Type</p>
						<span onClick={() => setShowTypeComp(true)}>
							<i className='fal fa-pen'></i>
						</span>
						<select
							id='eventType'
							name='eventType'
							required
							defaultValue={formData.tournament ? 'tournament' : 'cash game'}
							select={formData.tournament ? 'tournament' : 'cash game'}
							onChange={(e) => onChangeHandler(e)}>
							<option value=''>Event Type</option>
							<option value='cash game'>Cash game</option>
							<option value='tournament'>Tournament</option>
						</select>
					</div>

					<div className='card-rows'>
						<p>Game Type</p>
						<select
							id='gameType'
							name='gameType'
							required
							defaultValue={gameType}
							select={gameType}
							onChange={(e) => onChangeHandler(e)}>
							<option value=''>Game Type</option>
							<option value='THNL'>THNL</option>
							<option value='PLO'>PLO</option>
							<option value='MIX'>MIX</option>
						</select>
					</div>

					<div className='card-rows'>
						<p>Currency</p>
						<select
							name='currency'
							id='currency'
							required
							defaultValue={currency ? currency : ''}
							onChange={(e) => onChangeHandler(e)}>
							<option value='ILS'>ILS</option>
							<option value='USD'>USD</option>
							<option value='ERU'>ERU</option>
						</select>
					</div>
					<div className='card-rows'></div>

					<div className='card-rows'>
						<p>Players per Table</p>
						<select
							type='number'
							name='playersPerTable'
							required
							defaultValue={playersPerTable ? playersPerTable : '9'}
							id='playersPerTable'
							onChange={(e) => onChangeHandler(e)}>
							<option value='2'>2</option>
							<option value='3'>3</option>
							<option value='4'>4</option>
							<option value='5'>5</option>
							<option value='6'>6</option>
							<option value='7'>7</option>
							<option value='8'>8</option>
							<option value='9'>9</option>
							<option value='10'>10</option>
						</select>
						<p>Tables</p>
						<input
							type='number'
							name='numberOfTables'
							id='numberOfTables'
							required
							defaultValue={numberOfTables ? numberOfTables : 1}
							onChange={(e) => onChangeHandler(e)}
						/>
					</div>

					<div>
						<p>Description</p>
						<textarea
							name='description'
							form='event-form'
							value={description}
							onChange={(e) => onChangeHandler(e)}
							rows='4'>
							Description
						</textarea>
					</div>
					<div>
						<p>Address</p>
						<div className='address-line'>
							{/* <Address onChangeAddress={(e) => onChangeAddress(e)} /> */}
							{/* <MapComponent /> */}
						</div>
					</div>
				</div>
			</form>
			<div className='edit-modal-footer'>
				<button className='btn-light-outline' onClick={() => submit()}>
					Submit
				</button>
				<button
					className='btn-light-outline danger'
					onClick={() => {
						props.setDisplay(false);
						setFormData({});
					}}>
					Cancel
				</button>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => {
	return {
		show: state.modalReducer.showEvent,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		openEventType: () => dispatch(openEventType()),
		editEvent: (upDatedEvent) => dispatch(editEvent(upDatedEvent)),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(EditEvent);
