import * as actionTypes from './actionTypes';
import axios from 'axios';
import { setAlert } from './alretActions';
import { loadUser } from './authActions';

/*-----------------user clubs and events---------------*/
export const getAllClubs = (user) => async (dispatch) => {
	dispatch({ type: actionTypes.CLUBS_LOADING });
	try {
		const clubs = await axios.get('/api/clubs');
		let allClubs = [];

		clubs.data.map((club) => {
			if (
				user.pendingclubrequest.filter((clb) => clb._id === club._id).length ===
					0 &&
				user.regclubs.filter((clb) => clb._id === club._id).length === 0
			) {
				allClubs.push(club);
			}
		});
		dispatch({
			type: actionTypes.FETCH_ALL_CLUBS,
			payload: allClubs,
		});
	} catch (error) {
		console.log(error);
		const errors = error.response.data.errors;
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};

export const joinRequest = (clubId) => async (dispatch) => {
	dispatch({ type: actionTypes.CLUBS_LOADING });

	try {
		const res = await axios.put(
			`/api/clubs/join/${clubId}`
		);
		await dispatch(loadUser());
		await dispatch({ type: actionTypes.CLUB_JOIN_REQUEST });
		dispatch(setAlert('Your request was sent to club manager', 'success'));
	} catch (error) {
		dispatch(setAlert(error.response.data.msg, 'danger'));
		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};

export const cancelJoinRequest = (clubId) => async (dispatch) => {
	dispatch({ type: actionTypes.CLUBS_LOADING });
	try {
		const res = await axios.put(
			`/api/clubs/cancel-join-request/${clubId}`
		);
		await dispatch(setAlert('Your request to join was canceled', 'success'));
		await dispatch({ type: actionTypes.DELETE_CLUB_JOIN_REQUEST });
		dispatch(loadUser());
	} catch (error) {
		const errors = error.response.data.errors;
		errors.forEach((error) => {
			dispatch(setAlert(error.msg, 'danger'));
		});
		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};

export const leaveClub = (clubId) => async (dispatch) => {
	dispatch({ type: actionTypes.CLUBS_LOADING });
	try {
		const res = await axios.delete(
			`/api/clubs/un_join/${clubId}`
		);
		dispatch(setAlert('You are no longer a member of the club', 'primary'));
		dispatch({ type: actionTypes.LEAVE_CLUB });
		dispatch(loadUser());
	} catch (error) {
		const errors = error.response.data.errors;
		console.log(errors);
		for (const error of errors) {
			dispatch(setAlert(error.msg, 'danger'));
		}

		dispatch({
			type: actionTypes.CLUB_ERROR,
		});
	}
};
