import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { createClub } from '../../store/actions/managerActions';

const CreateClub = (props) => {
	const [formData, setFormData] = useState({
		clubname: '',
		clublogo: '',
		description: '',
	});
	const { clubname, clublogo, description } = formData;

	const onChangeHandler = (e) => {
		setFormData({
			...formData,
			[e.target.name]: e.target.value,
		});
	};
	const onSubmitHandler = (e) => {
		e.preventDefault();
		const newClub = {
			name: clubname,
			clublogo,
			description,
		};
		props.onSubmit(newClub);
	};

	if (clublogo.length > 0) {
		const logo = document.createElement('image');
		logo.src = URL.createObjectURL(clublogo);
		logo.setAttribute('width', '50px');
		logo.setAttribute('height', '50px');
		document.querySelector('.preview').appendChild(logo);
	}
	if (!props.clubLoading) {
		return <Redirect to='/admin' />;
	}
	return (
		<div className='club-main-form'>
			<h1 className='large text-primary'>Create New Club</h1>
			<form className='form' onSubmit={(e) => onSubmitHandler(e)}>
				{/* <div className='form-group'>
					<label for='clublogo'>Choose images to upload (PNG, JPG)</label>
					<div className='preview'>
						<input
							type='file'
							placeholder='Browse Image'
							name='clublogo'
							accept='.jpg, .jpeg, .png'
							value={clublogo}
							onChange={(e) => onChangeHandler(e)}
						/>
					</div>
				</div> */}
				<div className='form-group'>
					<input
						type='text'
						placeholder='Club Name'
						name='clubname'
						required
						value={clubname}
						onChange={(e) => onChangeHandler(e)}
					/>
				</div>
				<div className='form-group '>
					<input
						type='texterea'
						placeholder='club description'
						name='description'
						value={description}
						onChange={(e) => onChangeHandler(e)}
					/>
				</div>
				<div className='submit-div'>
					<input type='submit' className='btn btn-primary' value='Submit' />
				</div>
			</form>
		</div>
	);
};
const mapDispatchToProps = (dispatch) => {
	return {
		onSubmit: (club) => dispatch(createClub(club)),
	};
};
const mapStateToProps = (state) => {
	return {
		loading: state.authReducer.loading,
		clubLoading: state.managerReducer.clubLoading,
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(CreateClub);
