import React from 'react';
import { connect } from 'react-redux';
import { hideModal } from '../../store/actions/modalActions';
import {
	declineJoinRequest,
	approveJoinRequest,
	removeMember,
} from '../../store/actions/managerActions';
import { loadUser } from '../../store/actions/authActions';
import { useHistory } from 'react-router';

function Modal(props) {
	const history = useHistory();

	const confirm = () => {
		switch (props.action.action) {
			case 'declineJoinRequest':
				props.declineJoinRequest(props.action.clubId, props.action.userId);
				props.loadUser();
				props.hideModal();
				history.push(`edit-club/${props.clubId}`);
				break;
			case 'approveJoinRequest':
				props.approveJoinRequest(props.action.clubId, props.action.userId);
				props.loadUser();
				props.hideModal();
				history.push(`edit-club/${props.clubId}`);
				break;
			case 'removeMember':
				props.removeMember(props.action.clubId, props.action.userId);
				props.loadUser();
				props.hideModal();
				history.push(`edit-club/${props.clubId}`);
				break;
			default:
				break;
		}
	};
	return (
		<div className='modal-main' style={{ display: props.show }}>
			<div className='modal-body'>
				<h2 className={`modal-head ${props.class}`}>{props.header}</h2>
				<h3 className='modal-inner-body'>
					<strong>{props.body}</strong>
				</h3>
				<div className='modal-footer'>
					<button
						className='btn btn-primary-outline success'
						onClick={() => confirm()}>
						Confirm
					</button>
					<button
						className='btn btn-primary-outline danger'
						onClick={() => {
							props.hideModal();
							history.goBack();
						}}>
						Cancel
					</button>
				</div>
			</div>
		</div>
	);
}
const mapStateToProps = (state) => {
	return {
		header: state.modalReducer.header,
		body: state.modalReducer.body,
		action: state.modalReducer.action,
		show: state.modalReducer.show,
		class: state.modalReducer.class,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		hideModal: () => dispatch(hideModal()),
		declineJoinRequest: (clubId, userId) =>
			dispatch(declineJoinRequest(clubId, userId)),
		approveJoinRequest: (clubId, userId) =>
			dispatch(approveJoinRequest(clubId, userId)),
		removeMember: (clubId, userId) => dispatch(removeMember(clubId, userId)),
		loadUser: () => dispatch(loadUser()),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Modal);
