import React, { useState } from 'react';
import Address from '../../address/Address';
import { connect } from 'react-redux';
import { createEvent } from '../../../store/actions/eventActions';
import { openEventType } from '../../../store/actions/modalActions';
import MapComponent from '../../address/MapComponent';
import { useJsApiLoader } from '@react-google-maps/api';
import EventType from './EventType';
import { fromAddress } from 'react-geocode';
import { Fragment } from 'react';

const CreateEvent = (props) => {
	const [formData, setFormData] = useState({
		club: props.match.params.club_id,
		eventName: '',
		eventDate: new Date(),
		eventTime: '',
		location: {
			street: '',
			streetNumber: '',
			city: '',
			neighborhood: '',
			country: '',
			state: '',
			coordinates: '',
		},
		description: '',
		gameType: '',
		eventType: '',
		playersPerTable: 9,
		numberOfTables: 1,
		currency: 'ILS',
		remarks: '',
		smallBlind: '',
		bigBlind: '',
		minBuyin: '',
		maxBuyin: '',
		buyIn: '',
		startingChips: '',
		ante: '',
		timePerLevel: '',
		registerUntil: '',
		numberOfRe_entry: '',
	});

	const onChangeHandler = (e) => {
		if (e.target.localName === 'select') {
			const index = e.target.options.selectedIndex;
			if (e.target.id === 'eventType' && e.target.id !== '') {
				props.openEventType(formData);
			}
			// const elemName = e.target.id;
			setFormData({
				...formData,
				[e.target.id]: e.target[index].value,
			});
		} else {
			setFormData({
				...formData,
				[e.target.name]: e.target.value,
			});
		}
	};

	const onChangeAddress = (e) => {
		setFormData({ ...formData, location: e });
	};

	const setFields = (data) => {
		setFormData({ ...formData, ...data });
	};

	// const { isLoaded } = useJsApiLoader({
	// 	id: 'google-map-script',
	// 	googleMapsApiKey: process.env.REACT_APP_GOOGLE_API_KEY,
	// 	libraries: ['places'],
	// });
	// console.log(formData);
	const {
		club,
		eventName,
		eventDate,
		eventTime,
		location: {
			street,
			streetNumber,
			city,
			neighborhood,
			country,
			state,
			coordinates,
		},
		description,
		gameType,
		eventType,
		playersPerTable,
		numberOfTables,
		currency,
		remarks,
		smallBlind,
		bigBlind,
		minBuyin,
		maxBuyin,
		buyIn,
		startingChips,
		ante,
		timePerLevel,
		registerUntil,
		numberOfRe_entry,
	} = formData;

	const onSubmit = (e) => {
		e.preventDefault();
		const newEvent = {
			club,
			eventName,
			eventDate,
			eventTime,
			location: {
				street,
				streetNumber,
				city,
				neighborhood,
				country,
				state,
				coordinates,
			},
			description,
			gameType,
			eventType,
			playersPerTable,
			numberOfTables,
			currency,
			remarks,
			smallBlind,
			bigBlind,
			minBuyin,
			maxBuyin,
			buyIn,
			startingChips,
			ante,
			timePerLevel,
			registerUntil,
			numberOfRe_entry,
		};
		props.createEvent(newEvent);
	};

	return (
		<Fragment>
			<div className='create-event' onSubmit={(e) => onSubmit(e)}>
				<h2>New event for club {props.location.club}</h2>
				<EventType
					type={formData.eventType}
					eventTypeData={(data) => setFields(data)}
				/>
				<form id='event-form' className='card-columns'>
					<div className='card-rows'>
						<div className='form-group'>
							<p>Event Name</p>
							<input
								type='text'
								placeholder='Event Name'
								name='eventName'
								required
								value={formData.eventName}
								onChange={(e) => onChangeHandler(e)}
							/>
						</div>
						<div className='form-group'>
							<p>Date</p>
							<input
								type='datetime-local'
								placeholder='Event date'
								id='eventDate'
								name='eventDate'
								required
								value={formData.eventDate}
								onChange={(e) => onChangeHandler(e)}
							/>
						</div>
					</div>
					<div className='card-rows'>
						<div className='form-group'>
							<p>Type</p>
							<select
								id='eventType'
								name='eventType'
								required
								value={formData.eventType ? formData.eventType : ''}
								onChange={(e) => onChangeHandler(e)}
								defaultValue='cash game'>
								<option value=''>Event Type</option>
								<option value='cash game'>Cash game</option>
								<option value='tournament'>Tournament</option>
							</select>
						</div>

						<div className='form-group'>
							<p>Game</p>
							<select
								id='gameType'
								name='gameType'
								required
								value={formData.gameType ? formData.gameType : ''}
								defaultValue='THNL'
								onChange={(e) => onChangeHandler(e)}>
								<option value=''>Game Type</option>
								<option value='THNL'>THNL</option>
								<option value='PLO'>PLO</option>
								<option value='MIX'>MIX</option>
							</select>
						</div>

						<div className='form-group'>
							<p>Currency</p>
							<select
								name='currency'
								id='currency'
								required
								value={formData.currency ? formData.currency : ''}
								onChange={(e) => onChangeHandler(e)}>
								<option value='ILS'>ILS</option>
								<option value='USD'>USD</option>
								<option value='ERU'>ERU</option>
							</select>
						</div>
						<div className='form-group'>
							<p>Players / Table</p>
							<select
								type='number'
								name='playersPerTable'
								required
								value={
									formData.playersPerTable ? formData.playersPerTable : '9'
								}
								id='playersPerTable'
								defaultValue='9'
								onChange={(e) => onChangeHandler(e)}>
								<option value='2'>2</option>
								<option value='3'>3</option>
								<option value='4'>4</option>
								<option value='5'>5</option>
								<option value='6'>6</option>
								<option value='7'>7</option>
								<option value='8'>8</option>
								<option value='9'>9</option>
								<option value='10'>10</option>
							</select>
						</div>

						<div className='form-group'>
							<p>Tables</p>
							<input
								type='number'
								name='numberOfTables'
								id='numberOfTables'
								required
								value={formData.numberOfTables ? formData.numberOfTables : 1}
								onChange={(e) => onChangeHandler(e)}
							/>
						</div>
					</div>
					<div className='card-rows address'>
						<div className='card-columns'>
							<p>Description</p>
							<textarea
								name='description'
								form='event-form'
								value={formData.description}
								onChange={(e) => onChangeHandler(e)}
								rows='4'
								cols='40'>
								Description...
							</textarea>
						</div>
						<div className='card-columns'>
							<p>Address</p>
							<div className='address-line'>
								<Address onChangeAddress={(e) => onChangeAddress(e)} />
								{/* <MapCompoent /> */}
							</div>
						</div>
					</div>
					<div className='modal-footer'>
						<button className='btn-success btn'>Submit</button>
						<button
							className='btn-danger btn'
							onClick={() => props.history.goBack()}>
							Cancel
						</button>
					</div>
				</form>
			</div>
		</Fragment>
	);
};
const mapStateToProps = (state) => {
	return {
		show: state.modalReducer.showEvent,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		openEventType: () => dispatch(openEventType()),
		createEvent: (newEvent) => dispatch(createEvent(newEvent)),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(CreateEvent);
