import React from 'react';
import { connect } from 'react-redux';
import { loadUser } from '../../store/actions/authActions';
import ClubSmallCard from './ClubSmallCard';
import {
	getAllClubs,
	leaveClub,
	joinRequest,
	cancelJoinRequest,
} from '../../store/actions/clubActions';

// TODO- correct logic of join or un-join club - UI is not correct

const Clubs = (props) => {
	const clubs = props.clubs;

	return (
		<div className='clubs'>
			<div className='cards-container'>
				{clubs.length > 0 ? (
					clubs.map((club) => {
						return (
							<ClubSmallCard
								club={club}
								type={props.type}
								key={club._id}
								eventHandler={props.eventHandler}
							/>
						);
					})
				) : (
					<p>No clubs availble</p>
				)}
			</div>
		</div>
	);
};

const mapStateToProps = (state) => {
	return {
		allClubs: state.authReducer.allClubs,
		clubLoading: state.authReducer.clubLoading,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		loadUser: () => dispatch(loadUser()),
		joinRequest: (club_id) => dispatch(joinRequest(club_id)),
		cancelJoinRequest: (club_id) => dispatch(cancelJoinRequest(club_id)),
		leaveClub: (club_id) => dispatch(leaveClub(club_id)),
		getAllClubs: (user) => dispatch(getAllClubs(user)),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Clubs);
