import React from 'react';
import { connect } from 'react-redux';
import { Link, Redirect } from 'react-router-dom';
import { loadUser } from '../../store/actions/authActions';

const Landing = (props) => {
	
	if (props.isAuthenticated) {
		return <Redirect to='/dashboard' />;
	}

	return (
		<div className='landing'>
			<div className='content-container'>
				<h1 className='x-large'>Welcome to PokerPlace</h1>
				<p>Join, create and get updates on any poker game around you</p>
				<div className='buttons'>
					<div className='social-links card-rows'>
						<span>
							<i className='fab fa-facebook'></i>
						</span>
						<span>
							<i className='fab fa-linkedin'></i>
						</span>
						<span>
							<i className='fab fa-instagram-square'></i>
						</span>
						<span>
							<i className='fab fa-twitter-square'></i>
						</span>
						<span>
							<i className='fab fa-pinterest-square'></i>
						</span>
					</div>
					<div>
						<Link to='/register' className='btn btn-light-outline'>
							Sign Up
						</Link>
						<Link to='/login' className='btn btn-light-outline'>
							Login
						</Link>
					</div>
				</div>
			</div>
		</div>
	);
};

const mapStateToProps = (state) => {
	return {
		isAuthenticated: state.authReducer.isAuthenticated,
		isloading: state.authReducer.loading,
		user: state.authReducer.user,
	};
};
const mapDispatchToProps = (dispatch) => {
	return {
		loadUser: () => dispatch(loadUser()),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Landing);
