import React from 'react';
import { Link } from 'react-router-dom';

const EventSmallCard = (props) => {
	const {
		club,
		_id,
		eventLogo,
		eventName,
		eventDate,
		location,
		description,
		gameType,
		playersPerTable,
		numberOfTables,
		currency,
		cashGame,
		tournament,
	} = props.event;

	let date = new Date(eventDate).toLocaleString('he-IL', {
		day: '2-digit',
		month: '2-digit',
		year: '2-digit',
		hour12: false,
		hour: '2-digit',
		minute: '2-digit',
	});
	const dateArr = date.split('.');
	const formateddate = dateArr[0] + ' / ' + dateArr[1] + ' / ' + dateArr[2];

	const currencyIcon = () => {
		switch (currency) {
			case 'ILS':
				return <i className='fal fa-shekel-sign'></i>;
			case 'USD':
				return <i className='fal fa-dolar-sign'></i>;
			case 'ERU':
				return <i className='fal fa-euro-sign'></i>;
			default:
				return;
		}
	};

	const buttons = () => {
		switch (props.type) {
			case 'members':
				return (
					<ul>
						<li>
							<div onClick={() => props.eventHandler('join', _id)}>
								<i className='fal fa-chair-office'></i>
								<p>Book Seat</p>
							</div>
						</li>
					</ul>
				);
			case 'non-member':
				return (
					<ul>
						<li>
							<div
								onClick={() => props.eventHandler('clubJoinRequest', club._id)}>
								<i className='fal fa-paper-plane'></i>
								<p>Join Club</p>
							</div>
						</li>
					</ul>
				);
			case 'pending':
				return (
					<ul>
						<li>
							<div
								onClick={() =>
									props.eventHandler('cancelJoinRequest', club._id)
								}>
								<i className='fal fa-times'></i>
								<p>Pending</p>
							</div>
						</li>
					</ul>
				);
			case 'manager':
				return (
					<ul>
						<li>
							<div onClick={() => props.eventHandler('edit',props.event)}>
								<i className='fal fa-pen'></i>
								<p>Edit</p>
							</div>
						</li>
					</ul>
				);
			case 'player':
				return (
					<ul>
						<li>
							<Link
								to={{
									pathname: '/event',
									event: props.event,
									type: props.type,
									eventHandler: props.eventHandler,
								}}>
								<div>
									<i className='fal fa-info'></i>
									<p>More</p>
								</div>
							</Link>
						</li>
						<li>
							<div onClick={() => props.eventHandler('leave', _id)}>
								<i className='fal fa-sign-out'></i>
								<p>Leave</p>
							</div>
						</li>
					</ul>
				);
			default:
				return;
		}
	};

	return (
		<div className='profile-card event-card'>
			<div className='profile-header'>
				<div>
					<div
						className='background-div'
						style={{
							backgroundImage: `url(${
								eventLogo ? eventLogo : '/images/rip.jpg'
							})`,
						}}>
						<img src={club.clubLogo} alt='logo' />
					</div>
				</div>
			</div>
			<div className='btn-icons'>{buttons()}</div>
			<div className='profile-body'>
				<h3>{eventName}</h3>
				{cashGame ? (
					<div>
						<p>{gameType} Cash Game</p>
						<p>
							Blinds: {cashGame.smallBlind}/{cashGame.bigBlind} {currencyIcon()}
						</p>
					</div>
				) : (
					<div>
						<p>{gameType} Tournament</p>
						<p>
							Buyin: {tournament.buyIn} {currencyIcon()}
						</p>
					</div>
				)}
				<div>
					<p>
						Start: {formateddate} <i className='far fa-clock'></i>{' '}
					</p>
					<p>
						Availble Seats <strong>3/27</strong>
					</p>
				</div>
			</div>
			<div className='profile-footer'>
				<button className='btn btn-light-outline'>More</button>
			</div>
		</div>
	);
};

export default EventSmallCard;
