import React, { useState, useEffect, Fragment } from 'react';
import { Redirect, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import { editClub, clubLoad } from '../../store/actions/managerActions';
import ButtonsContainer from '../layout/ButtonsContainer';
import MemberCard from '../members/MemberCard';
import EventSmallCard from '../events/EventSmallCard';
import { showModal } from '../../store/actions/modalActions';
import EditEvent from '../events/EditEvent';

const EditClub = (props) => {
	const clubId = props.match.params.id;
	const [club, setClub] = useState({});
	const [type, setType] = useState('Events');
	const history = useHistory();

	useEffect(() => {
		setClub(props.clubs.filter((club) => club._id.toString() === clubId)[0]);
	}, []);

	const changeHandler = (e) => {
		const { name, value } = e.target;
		setClub({
			...club,
			[name]: value,
		});
	};
	const [display, setDisplay] = useState(false);
	const [event, setEvent] = useState({});
	const onSubmitHandler = () => {
		props.onSubmit(club, club._id);
	};

	// Buttons Logic
	const changeType = (e) => {
		setType(e.target.id);
	};

	if (!props.clubLoading) {
		return <Redirect to='/admin' />;
	}

	const declineRequest = (user) => {
		props.showModal({
			header: 'Decline Request',
			body:
				`Are you sure you want to decline ${user.name.toUpperCase()}` +
				'`' +
				`s Join requests?`,
			action: {
				action: 'declineJoinRequest',
				userId: user._id,
				clubId: clubId,
			},
			clubId: clubId,
			userId: user._id,
			class: 'danger',
		});
	};

	const approveRequest = (user) => {
		props.showModal({
			header: 'Approve Request',
			body:
				`Are you sure you want to approve ${user.name.toUpperCase()}` +
				'`' +
				`s Join requests?`,
			action: {
				action: 'approveJoinRequest',
				userId: user._id,
				clubId: clubId,
			},
			clubId: clubId,
			userId: user._id,
			class: 'success',
		});
	};

	const removeMember = (user) => {
		props.showModal({
			header: 'Remove Member',
			body: `Are you sure you want to un-member ${user.name.toUpperCase()}`,
			action: {
				action: 'removeMember',
				userId: user._id,
				clubId: clubId,
			},
			clubId: clubId,
			userId: user._id,
			class: 'danger',
			destination: '',
		});
	};

	return (
		<Fragment>
			{display ? <EditEvent closeModal={setDisplay} event={event} /> : null}
			<div className='club-edit-form'>
				<div className='form'>
					<div className='header'>
						<input
							type='text'
							id='name'
							placeholder='Club Name'
							name='name'
							required
							defaultValue={club.name}
							onChange={(e) => changeHandler(e)}
						/>

						<div className='header-content'>
							<div className='image'>
								<img src={club.clubLogo} alt='logo.. ' />
								<label className='btn upload-btn'>
									<i className='fal fa-image-polaroid'></i>
									<input
										type='file'
										accept='image/*'
										placeholder='logo'
										name='logo'
										defaultValue={club.clubLogo}
										onChange={(e) => changeHandler(e)}
										style={{ display: 'none' }}
									/>
								</label>
							</div>
							<div>
								<p>Description</p>
								<textarea
									type='text'
									rows='4'
									placeholder='club description'
									name='description'
									defaultValue={club.description}
									onChange={(e) => changeHandler(e)}
								/>
								<div className='submit-div'>
									<button
										className='btn-primary-outline'
										value='Submit'
										onClick={() => {
											onSubmitHandler();
										}}>
										Update
									</button>
								</div>
							</div>
						</div>
					</div>
				</div>
				<hr />
				<div className='card-rows'>
					<ButtonsContainer
						title1='Events'
						title2='Requests'
						title3='Members'
						func={changeType}
					/>
					<div className='add-btn'>
						<button
							className='btn-primary-outline'
							onClick={() => history.push(`/edit-event`)}>
							<i className='fal fa-plus'></i>
						</button>
					</div>
				</div>
				{/* <div className='cards-container'></div> */}
				{type === 'Events' ? (
					<div className='cards-container'>
						{club.events
							? club.events.map((event) => (
									<EventSmallCard
										event={event}
										key={event._id}
										type='manager'
										club={club}
										setEvent={setEvent}
										setDisplay={setDisplay}
									/>
							  ))
							: null}
					</div>
				) : type === 'Requests' ? (
					<div className='small-card-container profile'>
						{club.pendingRequest && club.pendingRequest.length > 0 ? (
							club.pendingRequest.map((req) => (
								<MemberCard
									key={req._id}
									member={req.user}
									type='pending'
									clubId={club._id}
									declineRequest={declineRequest}
									approveRequest={approveRequest}
								/>
							))
						) : (
							<p>No Pending Requests</p>
						)}
					</div>
				) : type === 'Members' ? (
					<div className='small-card-container'>
						{club.members && club.members.length > 0 ? (
							club.members.map((req) => (
								<MemberCard
									key={req._id}
									member={req.user}
									type='member'
									clubId={club._id}
									removeMember={removeMember}
								/>
							))
						) : (
							<p>No Club Members</p>
						)}
					</div>
				) : null}
				{/* <div className={type === 'events' ? 'display' : 'hide'}>
				<div className='card-rows'>
					<h3>Running Events</h3>
				</div>

				<div className='small-card-container'>
					{club.events
						? club.events.map((event) => (
								<EventSmallCard
									event={event}
									key={event._id}
									type='manager'
									club={club}
								/>
						  ))
						: null}
				</div>
			</div>
			<div className={type === 'Requests' ? 'display' : 'hide'}>
				<h3>Pending Requests</h3>
				<div className='small-card-container profile'>
					{club.pendingRequest && club.pendingRequest.length > 0 ? (
						club.pendingRequest.map((req) => (
							<MemberCard
								key={req._id}
								member={req.user}
								type='pending'
								clubId={club._id}
							/>
						))
					) : (
						<p>No Pending Requests</p>
					)}
				</div>
			</div>
			<div className={type === 'Members' ? 'display' : 'hide'}>
				<h3>Members</h3>
				<div className='small-card-container'>
					{club.members && club.members.length > 0 ? (
						club.members.map((req) => (
							<MemberCard
								key={req._id}
								member={req.user}
								type='member'
								clubId={club._id}
							/>
						))
					) : (
						<p>No Club Members</p>
					)}
				</div>
			</div> */}
			</div>
		</Fragment>
	);
};
const mapDispatchToProps = (dispatch) => {
	return {
		onSubmit: (club, club_id) => dispatch(editClub(club, club_id)),
		onLoading: () => dispatch(clubLoad()),
		showModal: (payload) => dispatch(showModal(payload)),
	};
};
const mapStateToProps = (state) => {
	return {
		loading: state.authReducer.loading,
		clubs: state.authReducer.user.createdclubs,
		clubLoading: state.managerReducer.clubLoading,
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(EditClub);
